import React, { useEffect, useState } from "react";
import leftArrowPic from "../left-arrow.svg";
import rightArrowPic from "../right-arrow.svg";

const ImageComponent = ({ listing }) => {
  const [activeIndex, setActiveIndex] = useState(0);
  const [xVal, setXVal] = useState(0);
  const [transformXStyle, setTransformXStyle] = useState({
    transform: "translateX(0px)",
  });
  const [imageXVal, setImageXVal] = useState(0);
  const [transformImageXStyle, setTransformImageXStyle] = useState({
    transform: "translateX(0px)",
  });

  useEffect(() => {
    setTransformXStyle({ transform: `translateX(${xVal}px)` });
    setTransformImageXStyle({ transform: `translateX(${imageXVal}px)` });
  }, [xVal, imageXVal]);

  const renderImages = () => {
    return listing.photos.map((photo, index) => {
      let imageClass = "images";
      if (index === activeIndex) imageClass = "images active";
      return (
        <>
          <img src={photo.url} className={`${imageClass}`} />
        </>
      );
    });
  };

  const imageNavigation = (val) => {
    const newVal = activeIndex + val;
    if (newVal >= 0 && newVal < listing.photos.length) {
      setActiveIndex(newVal);
    }
  };
  const navBarSlide = (indexInput) => {
    if (indexInput - 2 > 0 && indexInput < listing.photos.length - 2) {
      setXVal((indexInput - 2) * -20);
    } else {
      setXVal((prevState) => prevState * 0);
    }
  };

  const imageSlide = (indexInput) => {
    if (indexInput <= 0) setImageXVal(0);
    else if (indexInput === listing.photos.length) {
    } else setImageXVal(indexInput * -200);
  };

  const renderNavButtons = () => {
    return listing.photos.map((photo, index) => {
      let buttonClass = "buttons";
      if (index === activeIndex) buttonClass = "buttons active";

      return (
        <>
          <button
            className={buttonClass}
            onClick={() => {
              imageNavigation(index - activeIndex);
              navBarSlide(index);
              imageSlide(index);
            }}
          ></button>
        </>
      );
    });
  };

  return (
    <>
      <a
        href="#"
        onClick={(e) => {
          e.preventDefault();
          imageNavigation(-1);
          navBarSlide(activeIndex);
          imageSlide(activeIndex - 1);
        }}
        className="item1"
      >
        <img src={leftArrowPic} />
      </a>

      <div className="item2">
        <div className="imagesList" style={transformImageXStyle}>
          {renderImages()}
        </div>
      </div>

      <div className="item4">
        <ul className="imageButtons" style={transformXStyle}>
          {renderNavButtons()}
        </ul>
      </div>
      <a
        href="#"
        onClick={(e) => {
          e.preventDefault();
          imageNavigation(1);
          navBarSlide(activeIndex);
          imageSlide(activeIndex + 1);
        }}
        className="item3"
      >
        <img src={rightArrowPic} />
      </a>
    </>
  );
};

export default ImageComponent;
