import React, { useEffect } from "react";
import { connect } from "react-redux";
import ListingItem from "./ListingItem";
import { fetchData, fetchDataIncrement } from "../actions/listings";
import leftArrowPic from "../black-left-arrow.svg";
import rightArrowPic from "../black-right-arrow.svg";

function mapStateToProps(state) {
  return {
    listings: state.listings.listings,
    firstRender: state.listings.firstRender,
    startIndex: state.listings.startIndex,
    endIndex: state.listings.endIndex,
  };
}

function mapDispatchToProps(dispatch) {
  return {
    fetchData: () => {
      dispatch(fetchData);
    },
    fetchDataIncrement: () => {
      dispatch(fetchDataIncrement);
    },
    increment: () => {
      dispatch({ type: "INCREMENT" });
    },
    decrement: () => {
      dispatch({ type: "DECREMENT" });
    },
    triggerRender: () => {
      dispatch({ type: "TRIGGER_RENDER" });
    },
  };
}

const ListingCarousel = ({
  startIndex,
  endIndex,
  listings,
  fetchData,
  fetchDataIncrement,
  increment,
  decrement,
  triggerRender,
}) => {
  useEffect(() => {
    triggerRender();
    fetchData();
  }, []);

  if (!listings) return null;
  console.log("LISTINGS", listings);

  const iterateListings = () => {
    return listings.map((listing, index) => {
      if (index >= startIndex && index <= endIndex) {
        return <ListingItem cssId="listItem" key={index} listing={listing} />;
      }
    });
  };

  const assessIncrement = () => {
    if (endIndex >= listings.length - 3 && endIndex <= listings.length) {
      triggerRender();
      return fetchData();
    }
    return increment;
  };

  return (
    <>
      <h1 id="mainTitle">Listings with videos</h1>
      <a href="#" id="leftArrow" onClick={decrement}>
        <img src={leftArrowPic} />
      </a>
      {iterateListings()}
      <a href="#" id="rightArrow" onClick={assessIncrement()}>
        <img src={rightArrowPic} />
      </a>
    </>
  );
};

