import {
  UPDATE_LISTINGS,
  DECREMENT,
  INCREMENT,
  TRIGGER_RENDER,
} from "../actions/listings";

export default function listings(state = [], action) {
  const oldStartIndex =
    typeof state.startIndex === "undefined" ? 0 : state.startIndex;
  const oldEndIndex =
    typeof state.endIndex === "undefined" ? 3 : state.endIndex;

  switch (action.type) {
    case UPDATE_LISTINGS:
      const oldListings =
        typeof state.listings === "undefined" ? [] : state.listings;
      if (state.firstRender) {
        return {
          listings: oldListings.concat(action.listings),
          firstRender: false,
          startIndex: oldStartIndex,
          endIndex: oldEndIndex,
        };
      }
      return state;

    case DECREMENT:
      if (oldStartIndex > 0) {
        return {
          ...state,
          startIndex: oldStartIndex - 4,
          endIndex: oldEndIndex - 4,
        };
      }
      return state;

    case INCREMENT:
      return {
        ...state,
        startIndex: oldStartIndex + 4,
        endIndex: oldEndIndex + 4,
      };
    case TRIGGER_RENDER:
      return {
        ...state,
        firstRender: true,
      };
    default:
      return state;
  }
}
